@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;900&display=swap";
@import "https://fonts.googleapis.com/css?family=Bungee";
*, body {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  -moz-osx-font-smoothing: grayscale;
  font-family: Poppins, sans-serif;
  font-weight: 400;
}

html, body {
  background-color: #141e30;
  height: 100%;
  overflow: hidden;
}

.hidden {
  display: none;
}

.survey--container {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.survey--container .survey--core {
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
  position: relative;
}

.survey--core .survey--items {
  text-align: left;
  border: 3px solid #fff;
  border-radius: 10px;
  padding: 20px;
  transition: all .4s;
  display: inline-block;
}

.reduced_size {
  padding: 0;
}

.survey--core h3 {
  color: #fff;
  text-align: left;
  margin-bottom: 5px;
  font-size: 28px;
  font-weight: 600;
}

.survey--core h3.form-title {
  margin-bottom: 30px;
}

.survey--core p {
  color: #fff;
  text-align: left;
  margin-bottom: 30px;
  font-size: 17px;
  font-weight: 300;
  line-height: 20px;
}

.survey--core select {
  text-align: left;
  color: #000;
  background-color: #fff;
  border: 0;
  border-radius: 6px;
  outline: 0;
  width: 100%;
  margin-top: 16px;
  padding: 9px 20px;
  font-size: 15px;
  font-weight: 300;
  transition: all .3s;
}

.btn {
  text-align: center;
  background-color: #b3b3b3;
  border: none;
  border-radius: 4px;
  flex-grow: 0;
  width: 300px;
  padding: 10px;
  font-family: Bungee, sans-serif;
  font-size: 20px;
}

.btn--survey {
  text-align: center;
  color: #fff;
  background-color: #585858;
  width: 190px;
  height: 45px;
}

.btn--survey:link, .btn--survey:visited {
  color: #fff;
  text-decoration: none;
}

.btn:active {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 3px solid #fff;
}

.btn--area {
  justify-content: center;
  margin-top: 30px;
  display: flex;
}
